<template>
  <div class="d-flex">
    <navigation />
    <div class="flex-fill" style="background: #EEF2F5;">
      <div class="row m-3">
        <div class="col-4">
          <a class="btn btn-primary" href="/dashboard">Overall</a>
          <a class="btn" href="/performance">Response</a>
          <a class="btn" href="/performance">Agent Performance</a>
        </div>
        <div class="col-8 d-flex flex-row-reverse">
          <button
            @click="selected = '7days'"
            class="btn"
            :class="[selectedClass('7days')]"
          >Last 7 Days</button>
          <button
            @click="selected = '3days'"
            class="btn"
            :class="[selectedClass('3days')]"
          >Last 3 Days</button>
          <button
            @click="selected = 'yesterday'"
            class="btn"
            :class="[selectedClass('yesterday')]"
          >Yesterday</button>
          <button @click="selected = 'today'" class="btn" :class="[selectedClass('today')]">Today</button>
        </div>
      </div>
      <div class="row m-3">
        <div class="col-4">
          <status-pie-chart :data="statusPieChartData" :loading="loading" class="chart" />
        </div>
        <div v-if="1 == 1" class="col-4">
          <enquiry-pie-chart :data="enquiryPieChartData" :loading="loading" class="chart" />
        </div>
        <div v-if="1 == 1" class="col-4">
          <channel-pie-chart
            :data="channelPieChartData"
            :loading="loading"
            class="chart"
            v-if="channelPieChartData.length>1"
          />
        </div>
      </div>
      <div class="row m-3">
        <div v-if="1 == 1" class="col-6">
          <tags-bar-chart :data="tagsBarChartData" :loading="loading" class="chart" />
        </div>
        <div v-if="1 == 1" class="col-6">
          <product-bar-chart :data="productBarChartData" :loading="loading" class="chart" />
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import Navigation from "@/components/Navigation";
// import Multiselect from "vue-multiselect";
import axios from "axios";
// import ApexCharts from "apexcharts";
// // import VueApexCharts from "vue-apexcharts";
import StatusPieChart from "./StatusPieChart";
import ChannelPieChart from "./ChannelPieChart";
import TagsBarChart from "./TagsBarChart";
import EnquiryPieChart from "./EnquiryPieChart";
import ProductBarChart from "./ProductBarChart";
// import ChannelsBarChart from "./Pending/ChannelsBarChart";

export default {
  components: {
    Navigation,
    StatusPieChart,
    ChannelPieChart,
    EnquiryPieChart,
    TagsBarChart,
    ProductBarChart
  },
  data() {
    return {
      selected: "today",
      channelPieChartData: [],
      enquiryPieChartData: {},
      productBarChartData: [],
      statusPieChartData: {},
      tagsBarChartData: [],
      loading: false
    };
  },
  methods: {
    selectedClass(p) {
      return p == this.selected ? "btn-primary" : "";
    },
    async getDashboardData() {
      this.loading = true;
      const res = await axios
        .get("dashboard/GetDashboardData?dateFilter=" + this.selected)
        .then(res => {
          if (res.status == 200) {
            this.channelPieChartData = res.data.channelPieChart;
            console.log(this.channelPieChartData.length);
            this.enquiryPieChartData = res.data.enquiryPieChart;
            // console.log(this.enquiryPieChartData);
            this.productBarChartData = res.data.productBarChart;
            // console.log(this.productBarChartData);
            this.statusPieChartData = res.data.statusPieChartData;
            // console.log(this.statusPieChartData);
            this.tagsBarChartData = res.data.tagsBarChart;
            // console.log(this.tagsBarChartData);
            this.loading = false;
          }
        });
    }
  },
  mounted() {
    this.getDashboardData();
  },

  watch: {
    selected() {
      this.getDashboardData();
    }
  }
};
</script>
<style>
.box {
  box-shadow: 0 11.5px 6.5px -3px rgba(0, 0, 0, 0.024),
    0 18.1px 21.9px -3px rgba(0, 0, 0, 0.035),
    0 28px 98px -3px rgba(0, 0, 0, 0.07);
}
.custom-avatar.avatar table[data-v-4ffd1741] {
  width: 40px;
  height: 40px;
}

.custom-avatar.avatar table[data-v-4ffd1741] {
  width: 40px;
  height: 40px;
}
</style>
<style lang="scss" scoped>
.chart {
  background: #fff;
  border-radius: 18px;
  padding: 15px;
}
.loading-animate {
  animation: colorFade 1.2s ease-out infinite;
}
@keyframes colorFade {
  0% {
    background: #fff;
  }
  50% {
    background: rgba(0, 0, 0, 0.2);
  }
  100% {
    background: #fff;
  }
}
</style>
