<template>
  <div class="d-flex" :class="{'loading-animate': loading}">
    <div class="w-100 m-2">
      <v-chart :options="Options" :style="style" />
    </div>
  </div>
</template>
<script>
import axios from "axios";
import ECharts from "vue-echarts";
import "echarts/lib/chart/pie";
import "echarts/lib/component/legend";
import "echarts/lib/component/title";
import "echarts/lib/component/tooltip";

export default {
  props: {
    data: {
      type: Array
    },
    loading: {
      type: Boolean
    }
  },
  components: {
    "v-chart": ECharts
  },
  data() {
    return {
      chartData: [],
      style: {
        width: "100%"
      }
    };
  },

  methods: {
    async getPieChartData() {
      const res = await axios
        .get("dashboard/channelPieChart?dateFilter=" + this.filter)
        .then(res => {
          if (res.status == 200) this.chartData = res.data;
        });
    }
  },
  mounted() {
    this.getPieChartData();
  },
  computed: {
    Options() {
      let obj = {
        color: [
          "#FF3F52",
          "#FFBF00",
          "#0053FF",
          "#575fcf",
          "#05c46b",
          "#485460",
          "#808e9b"
        ],
        title: {
          text: "Channel"
        },
        tooltip: {
          trigger: "item",
          formatter: "{a} <br/>{b} : {c} ({d}%)"
        },
        legend: {
          top: "bottom",
          left: "center"
        },
        series: [
          {
            top: "15px",
            name: "Count",
            type: "pie",
            radius: "60%",
            data: this.chartData
            // emphasis: {
            //   itemStyle: {
            //     shadowBlur: 10,
            //     shadowOffsetX: 0,
            //     shadowColor: "rgba(0, 0, 0, 0.5)"
            //   }
            // }
          }
        ]
      };
      return obj;
    }
  }
};
</script>
<style lang="scss"></style>
