<template>
  <div class="d-flex" :class="{'loading-animate': loading}">
    <div class="m-2 w-100">
      <v-chart :options="Options" :style="style" />
    </div>
  </div>
</template>
<script>
import axios from "axios";
import ECharts from "vue-echarts";
import "echarts/lib/chart/bar";
import "echarts/lib/component/legend";
import "echarts/lib/component/title";

export default {
  props: {
    data: {
      type: Array
    },
    loading: {
      type: Boolean
    }
  },
  components: {
    "v-chart": ECharts
  },
  data() {
    return {
      channelList: [],
      option: {
        color: ["#2F4554", "#61A0A8", "#E5323E"],
        title: {
          text: "Enquiry Type by Product Categories"
        },
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "shadow"
          }
        },
        legend: {
          // data: ["Car", "Plane", "Train", "Ship", "Bicycle"]
          left: "right"
        },
        grid: {
          left: "3%",
          right: "4%",
          bottom: "3%",
          containLabel: true
        },
        xAxis: {
          type: "value",
          minInterval: 1,
          boundaryGap: [0, 0.01]
        },
        yAxis: {
          type: "category",
          data: []
        },
        series: []
      },
      style: {
        width: "100%",
        minHeight: "500px"
      }
    };
  },

  methods: {
    async getBarChartData() {
      const res = await axios
        .get("dashboard/ProductBarChart?dateFilter=" + this.filter)
        .then(res => {
          this.option.yAxis.data = res.data.map(x => x.name);
          let countList = res.data;
          let enquiry = {
            type: "bar",
            name: "Enquiry",
            data: countList.map(x => x.enquiryCount)
          };
          let feedback = {
            type: "bar",
            name: "Feedback",
            data: countList.map(x => x.feedbackCount)
          };
          let complaint = {
            type: "bar",
            name: "Complaint",
            data: countList.map(x => x.complaintCount)
          };

          this.option.series = [enquiry, feedback, complaint];
        });
    },
    getData() {
      this.option.yAxis.data = this.data.map(x => x.name);
      let countList = this.data;
      let enquiry = {
        type: "bar",
        name: "Enquiry",
        data: countList.map(x => x.enquiryCount)
      };
      let feedback = {
        type: "bar",
        name: "Feedback",
        data: countList.map(x => x.feedbackCount)
      };
      let complaint = {
        type: "bar",
        name: "Complaint",
        data: countList.map(x => x.complaintCount)
      };

      this.option.series = [enquiry, feedback, complaint];
    }
  },
  mounted() {},
  computed: {
    Options() {
      return this.option;
    }
  },
  watch: {
    data() {
      this.getData();
    }
  }
};
</script>
<style lang="scss" scoped>
  
</style>
