<template>
  <div class="d-flex" :class="{'loading-animate': loading}">
    <div class="m-2 w-100 ">
      <v-chart :options="options" :style="style" />
    </div>
  </div>
</template>
<script>
import axios from "axios";
import ECharts from "vue-echarts";
import "echarts/lib/chart/pie";
import "echarts/lib/component/legend";
import "echarts/lib/component/title";
import "echarts/lib/component/tooltip";

export default {
  props: {
    data: {
      type: Object
    },
    loading: {
      type: Boolean
    }
  },
  components: {
    "v-chart": ECharts
  },
  data() {
    return {
      options: {
        color: ["#0053FF", "#FFBF00", "#FF3838"],
        title: {
          text: "Enquiry Type"
        },
        tooltip: {
          trigger: "item",
          formatter: "{a} <br/>{b} : {c} ({d}%)"
        },
        legend: {
          top: "bottom",
          left: "center"
        },
        series: [
          {
            top: "15px",
            name: "Count",
            type: "pie",
            radius: "60%",
            data: []
            // emphasis: {
            //   itemStyle: {
            //     shadowBlur: 10,
            //     shadowOffsetX: 0,
            //     shadowColor: "rgba(0, 0, 0, 0.5)"
            //   }
            // }
          }
        ]
      },
      style: {
        width: "100%"
      }
    };
  },

  methods: {
    async getPieChartData() {
      const res = await axios
        .get("dashboard/EnquiryPieChart?dateFilter=" + this.filter)
        .then(res => {
          if (res.status == 200) {
            let enquiry = { name: "Enquiry", value: res.data.enquiry };
            let feedback = { name: "Feedback", value: res.data.feedback };
            let complaint = { name: "Complaint", value: res.data.complaint };
            this.options.series[0].data = [enquiry, feedback, complaint];
          }
        });
    },
    getData() {
      let enquiry = { name: "Enquiry", value: this.data.enquiry };
      let feedback = { name: "Feedback", value: this.data.feedback };
      let complaint = { name: "Complaint", value: this.data.complaint };
      this.options.series[0].data = [enquiry, feedback, complaint];
    }
  },
  mounted() {},
  computed: {},
  watch: {
    data() {
      this.getData();
    }
  }
};
</script>
<style lang="scss"></style>
