<template>
  <div class="d-flex" :class="{'loading-animate': loading}">
    <div class="m-2 w-100">
      <v-chart :options="Options" :style="style" />
    </div>
  </div>
</template>
<script>
import axios from "axios";
import ECharts from "vue-echarts";
import "echarts/lib/chart/pie";
import "echarts/lib/component/legend";
import "echarts/lib/component/title";
import "echarts/lib/component/tooltip";

export default {
  props: {
    data: {
      type: Object
    },
    loading: {
      type: Boolean
    }
  },
  components: {
    "v-chart": ECharts
  },
  data() {
    return {
      toReply: 0,
      followUp: 0,
      responded: 0,
      style: {
        width: "100%"
      }
    };
  },

  methods: {
    async getPieChartData() {
      const res = await axios
        .get("dashboard/StatusPieChart?dateFilter=" + this.filter)
        .then(res => {
          this.toReply = res.data.toReply;
          this.followUp = res.data.followUp;
          this.responded = res.data.responded;
        });
    }
  },
  mounted() {
    // this.getPieChartData();
  },
  computed: {
    Options() {
      let obj = {
        color: ["#ff3838", "#FFBF00", "#0053FF"],
        title: {
          text: "Response Status"
        },
        tooltip: {
          trigger: "item",
          formatter: "{a} <br/>{b} : {c} ({d}%)"
        },
        legend: {
          top: "bottom",
          left: "center"
        },

        series: [
          {
            top: "15px",
            name: "Count",
            type: "pie",
            radius: "60%",
            data: [
              { value: this.data.toReply, name: "To Reply" },
              { value: this.data.followUp, name: "Follow Up" },
              { value: this.data.responded, name: "Responded" }
            ]
            // emphasis: {
            //   itemStyle: {
            //     shadowBlur: 10,
            //     shadowOffsetX: 0,
            //     shadowColor: "rgba(0, 0, 0, 0.5)"
            //   }
            // }
          }
        ]
      };
      return obj;
    }
  }
};
</script>
<style lang="scss"></style>
